import React from 'react';

import PySandpack, { PySandpackUtil } from 'py_sandpack';

import Header from './Header';

const DEFAULT_CODE = `# Example code
import pandas as pd
import matplotlib.pyplot as plt

data = {
    'Month': ['January', 'February', 'March', 'April', 'May'],
    'Sales': [100, 150, 200, 130, 170]
}
df = pd.DataFrame(data)

print(df)

plt.figure(figsize=(8, 6))
df.plot(x='Month', y='Sales', kind='line', marker='o', title="Monthly Sales", legend=True)

plt.xlabel("Month")
plt.ylabel("Sales")
plt.grid(True)

plt.show()
`

function App() {
  return (
    <>
      <Header />
      <hr style={{ opacity: 0.2 }} />
      <div style={{ width: "99vw", height: "85vh" }}>
        <PySandpack codes={PySandpackUtil.UrlUtil.searchParam2Codes() ?? { "main.py": DEFAULT_CODE }} lang="python" />
      </div>
      <br />
      <iframe style={{ width: "99vw", height: "15vh", border: 0, }} src="https://yoojeeho.github.io/site/ads.html" />
    </>
  );
}

export default App;
